.projectsContainer {
    flex-direction: column;
    justify-content: flex-start;
}

.projectsBlock {
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: 100vh;
    background-color: #f8e8c1;
}

.title {
    padding: 10px;
    background-color: #eeffee;
}

.projects {
    display: flex;
    justify-content: space-around;
    border: 1px solid;
    width: 100%;
    flex-wrap: wrap;
}
