.project {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 350px;
    background-color: #eeffee;
}

.imgContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 180px;
    width: 100%;
    background-image: url("https://iso.500px.com/wp-content/uploads/2016/03/stock-photo-142984111.jpg");
    background-size: cover;
    background-position: center;
}

.imgContainer a {
    height: 20px;
    width: 100px;
    background-color: #eeffee;
    opacity: 0.6;
    border-radius: 5px;
}